.products {
  display: flex;
  gap: 28px;
  
  &_list {
    display: flex;
    flex-direction: column;
    flex-basis: 70%;
    gap: 28px;

    box-sizing: border-box;
    padding: 12px 0;

  }

  &_container {
    display: grid;
    grid-template-columns: repeat(3, minmax(100px, 1fr));
    gap: 28px;
    width: 100%;
  }

  &_search {
    position: relative;
    flex-basis: 30%;
  }

  &_form {
    position: sticky;
    top: 28px;
    box-sizing: border-box;
    padding: 20px;
    border-radius: 20px;
    box-shadow: var(--default-box-shadow);
  }
}